import logo from "./logo.svg";
import "./App.css";
import Card from "./Card.js";
import Cart from "./Cart.js";
import Cartmani from "./Cartmani.js";
import { useState } from "react";
function App() {
  var sno=0;
  var items = [
    { name: "Item one", price: 15, description: "Mobile" ,no:1},
    { name: "Item two", price: 25, description: "Books", no:1},
    { name: "Item three", price: 35, description: "Automobile",no:1 },
    { name: "Item four", price: 45, description: "Technology" ,no:1},
  ];
  const [now, manipulate] = useState([]);
  var cart = function (i, remove) {
    var check=false;
   if(now.length>0){
    for(var j in now){
      if(now[j].name===i.name){
        console.log(i.name)
        if(remove&&now[j].no>0){
        now[j].no--;
        }else if(!remove)
        now[j].no++;
        check=true;
      }
    }
    manipulate([...now])
  }
    if(!check)
    manipulate([...now,i]);
  
  }
  return (
    <div class="container">
      <div class="row">
        <div class="col-lg-4">
          <h1 class="my-4">Shop Name</h1>
          <div>
            <Cart pass={now} eve={cart}></Cart>
          </div>
        </div>
        <div class="col-lg-8">
          <div class="row">
            {items.map((item) => {
              return <Card props={item} eve={cart}></Card>;
            })
            }
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
