import React from "react"
import Card from "./Card";
import App from "./App.js";
import Cart from "./Cart.js";

function Cartmani(props){
   
       return(
           <tr>
               <td>
                {props.s}
               </td>
               <td>
               {props.p.name}
               </td>
               <td>
               {props.p.no}
              </td>
              <td>
              <button type="button"  class="minus" onClick={()=>{
                props.eve(props.p, true)
              }}>
                 -
               </button>
              </td>
               <td>
                 {props.p.price*props.p.no}$
               </td>
           </tr>  
       );  
    
}

export default Cartmani;